<template>
  <div>
    <!-- <div id="canvas-frame"></div> -->
  </div>
</template>

<script>
import * as THREE from "three";
// 控制器
import { OrbitControls } from "three/examples/jsm/controls/OrbitControls";
// 性能监视器
import Stats from "@/assets/js/Stats.js";
// 动画引擎
import { TWEEN } from "@/assets/js/Tween.js";
import skyTexture from "@/assets/image/sky.jpg";
console.log(THREE, "three----");
export default {
  data() {
    return {
      statsObj: null,
    };
  },
  created() {},
  mounted() {
    this.threeCamera();
    this.statsObj = this.stats();
  },
  methods: {
    // 性能监视器
    stats() {
      var stats = new Stats();
      stats.setMode(1); // 0: fps, 1: ms
      // 将stats的界面对应左上角
      stats.domElement.style.position = "absolute";
      stats.domElement.style.left = "0px";
      stats.domElement.style.top = "0px";
      document.body.appendChild(stats.domElement);
      return stats;
    },
    threeCamera() {
      let that = this;
      // console.log(THREE, "THREE----", new THREE.Vector3(100, 0, -100));
      const camera = new THREE.PerspectiveCamera(
        45,
        window.innerWidth / window.innerHeight,
        0.01,
        10
      );
      // let width = window.innerWidth;
      // let height = window.innerHeight;
      // const camera = new THREE.OrthographicCamera(width / - 2, width / 2, height / 2, height / - 2, 0.01, 10);
      console.log(camera, "camera--------");
      camera.position.z = 2;
      camera.position.x = 0;
      camera.position.y = 0.2;
      // camera.up.x = 1;
      // camera.up.y = 2;
      // camera.up.z = 2;
      // camera.lookAt({
      //           x : 0,
      //           y : 0,
      //           z : 0
      //       });
      // var helper = new THREE.GridHelper( 1000, 50 );
      // helper.setColors( 0x0000ff, 0x808080 );
      const scene = new THREE.Scene();
      // 加载背景
      scene.background = new THREE.TextureLoader().load(skyTexture);
      console.log(scene, "scene-------");
      // 添加光源
      let light = new THREE.DirectionalLight(0x00ffff);
      light.position.set(0, 10, 1).normalize();
      scene.add(light);
      // 确定画布渲染的样子--正方体or长方体   一般就前三个参数为 画布的x,y,z轴
      const geometry = new THREE.BoxGeometry(0.1, 0.1, 0.1);
      const geometry1 = new THREE.BoxGeometry(0.2, 0.1, 0.2);
      /**
       * @MeshBasicMaterial -- 这是一种很基本的材质, 可以将颜色渲染到Geometry的表面, 但不能渲染 图片纹理
       * */
      const material = new THREE.MeshNormalMaterial();
      const material1 = new THREE.MeshBasicMaterial();
      // material1.color = {
      //   b:0,g:0,r:1
      // }
      // material1.vertexColors = false;
      // material1.opacity = 0.1
      console.log(material, "material---", material1);
      const mesh = new THREE.Mesh(geometry, material);
      const mesh1 = new THREE.Mesh(geometry1, material1);
      mesh1.position.x = 0.2;
      mesh1.position.y = 0.2;
      mesh1.rotation.x = 0.5;
      mesh1.rotation.y = 0.2;
      console.log(mesh1, "mesh1-----");
      // this.initTween(mesh);
      scene.add(mesh);
      scene.add(mesh1);
      const renderer = new THREE.WebGLRenderer({ antialias: true });
      renderer.setSize(window.innerWidth, window.innerHeight);

      renderer.setAnimationLoop(animation);
      document.body.appendChild(renderer.domElement);
      // 控制器
      let controls = new OrbitControls(camera, renderer.domElement);
      controls.target.set(0, 0, 0);
      controls.enableDamping = true;
      controls.enablePan = false;
      controls.enableZoom = false;
      // 垂直旋转角度限制
      controls.minPolarAngle = -100.4;
      controls.maxPolarAngle = 100.8;
      // 水平旋转角度限制
      controls.minAzimuthAngle = -100.9;
      controls.maxAzimuthAngle = 100.9;

      // animation
      mesh.rotation.x = 0.2;
      function animation(time) {
        // mesh.rotation.x = time / 2000;
        mesh.rotation.y = time / 1000;
        mesh1.rotation.y = time / 1000;
        // mesh.rotateX(0.005)
        // mesh.rotateY(0.01)
        // mesh.rotateZ(0.01)
        // if(camera.position.z>10){
        //     camera.position.z = 0.3;
        // }
        // if(camera.position.x>1.7){
        //     camera.position.x = 0;
        // }
        // camera.position.x += 0.005;
        // camera.position.z += 0.01;
        renderer.render(scene, camera);
        that.statsObj.update();
        // TWEEN.update();
      }
    },
    initTween(mesh) {
      new TWEEN.Tween(mesh.position)
        .to({ x: -400 }, 3000)
        .repeat(Infinity)
        .start();
    },
  },
};
</script>

<style lang="scss" scoped>
</style>